.App {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
  background-color: #f9f9f9;
  color: #333;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  color: #333;
  font-family: 'Arial', sans-serif;
}

.task-form {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.task-form input {
  width: 60%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
}

.task-form button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.task-form button:hover {
  background-color: #0056b3;
}

.task-list {
  list-style: none;
  padding: 0;
}

.task-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  color: #333;
}

.task-list li:hover {
  transform: scale(1.05);
}

.completed {
  text-decoration: line-through;
  color: green;
}

.task-list button {
  padding: 5px 10px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.task-list button:nth-child(2) {
  background-color: #dc3545;
}

.task-list button:hover {
  opacity: 0.8;
}

.task-status {
  font-size: 16px;
  margin-top: 20px;
}

.task-controls {
  margin-top: 20px;
}

.task-controls button {
  padding: 10px 15px;
  margin: 5px;
  background-color: #17a2b8;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.task-controls button:hover {
  background-color: #138496;
}
